@import url(https://fonts.googleapis.com/css?family=Cormorant+Garamond|Tangerine&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  word-break: keep-all; }

img {
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  width: 100%; }
  img.ril__image {
    width: auto; }

a {
  text-decoration: none;
  color: inherit; }

ul, li {
  list-style: none; }

h1 {
  margin: 0; }

.cf:after {
  content: '';
  display: table;
  clear: both; }

h1, h2, h3, h4, h5 {
  font-weight: 300; }

body {
  font-family: 'Cormorant Garamond', serif;
  color: #333;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

input {
  height: 50px;
  padding: 0 10px;
  margin-bottom: 20px;
  border-radius: 4px;
  width: 60%; }

textarea {
  padding: 10px;
  border-radius: 4px;
  width: 60%;
  resize: none;
  margin-bottom: 20px; }

button {
  padding: 10px 25px;
  height: 50px;
  width: 200px;
  background: #ff5f5f;
  border-radius: 4px;
  color: #fff; }

nav {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  width: 100%;
  padding: 10px 30px; }
  nav .navList {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    text-align: center;
    font-size: 20px;
    text-transform: capitalize;
    margin: 0 auto; }
    nav .navList li {
      position: relative; }
    nav .navList a {
      padding: 10px; }
      nav .navList a::after {
        -webkit-transition: .3s;
        transition: .3s;
        content: '';
        background: #333;
        height: 3px;
        position: absolute;
        width: 100%;
        bottom: -10px;
        left: 0;
        -webkit-transform: scaleX(0);
                transform: scaleX(0); }
      nav .navList a:hover::after, nav .navList a.active::after {
        -webkit-transform: scaleX(0.8);
                transform: scaleX(0.8); }

.logo, .intro .text {
  font-family: "Tangerine", cursive; }

section {
