        background-size: cover; }
      section.content .header.location {
        background: url(/static/media/location.46e217b6.png) no-repeat center center;
        background-size: cover; }
      section.content .header.contact {
        background: url(/static/media/location.46e217b6.png) no-repeat center center;
        background-size: cover; }
      section.content .header.after {
