.intro {
  background: url(/static/media/main.43844a6b.jpg) no-repeat center center;
  background-size: cover;
  height: 100vh; }
  .intro .text {
    position: relative;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 30%;
    width: 50%; }
    .intro .text h1 {
      font-size: 8vw;
      font-weight: 100; }
