.gallaryWrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 20px; }
  .gallaryWrapper .imageBox {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer; }
    .gallaryWrapper .imageBox img {
      -webkit-transition: .3s;
      transition: .3s; }
      .gallaryWrapper .imageBox img:hover {
        -webkit-transform: scale(1.1);
                transform: scale(1.1); }
    .gallaryWrapper .imageBox.vertical {
      grid-row: span 2; }
    .gallaryWrapper .imageBox.one {
      grid-column: span 2;
      grid-row: span 2; }

.formWrap {
  background: #FFDBDB;
  text-align: center;
  padding: 50px; }
  .formWrap h2 {
    margin-bottom: 20px;
    font-size: 3vw;
